import React, { useState, useEffect } from "react";
import { pizzas } from "../../constants";
import "./ProductsModal.scss";

const ProductsModal = ({ open, setOpen }) => {
  const [imagesReady, setImagesReady] = useState(false);

  function allLoaded() {
    setImagesReady(true);
  }

  let number = 0;

  useEffect(() => {
    pizzas.forEach((pizza) => {
      const img = new Image();
      img.src = pizza.modalPizza;
      img.onload = () => {
        number++;
        if (number === pizzas.length) {
          allLoaded();
        }
      };
    });
  }, [number]);

  const onClick = () => {
    window.scroll({
      top: 0,
      left: 0,
    });
  };

  return open ? (
    <div className="productsModal">
      <div className="productsContainer">
        {pizzas.map((pizza) => {
          return (
            <a
              className="pizzaContainer"
              key={pizza.id}
              onClick={() => onClick()}
              href={pizza.path}
            >
              {imagesReady ? (
                <div
                  style={{
                    backgroundImage: `url(${pizza.modalPizza})`,
                    backgroundPosition: "center",
                    backgroundSize: "contain",
                    backgroundRepeat: "no-repeat",
                    height: "68%",
                    maxHeight: "68%",
                    width: "80%",
                  }}
                ></div>
              ) : null}
              {pizza.name}
            </a>
          );
        })}
        {/* workaround for removed pizza and proper layout */}
        <div className="pizzaContainer"/>
        <div className="pizzaContainer"/>
      </div>
    </div>
  ) : null;
};

export default ProductsModal;
